:root,
[data-theme=light] {
  --black: #000000;
  --white: #ffffff;
  --gray-50: #f2f2f2;
  --gray-100: #e6e6e6;
  --gray-200: #cccccc;
  --gray-300: #b3b3b3;
  --gray-400: #999999;
  --gray-500: #808080;
  --gray-600: #666666;
  --gray-700: #4d4d4d;
  --gray-800: #333333;
  --gray-900: #1a1a1a;
  --gray-950: #0d0d0d;
  --primary: #0d6efd;
  --secondary: #666666;
  --success: #198754;
  --info: #0dcaf0;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #e6e6e6;
  --dark: #1a1a1a;
}

html {
  font-size: 1rem;
}

body {
  font-size: 1rem;
  font-family: Arial, Helvetica, sans-serif;
  color: #333333;
}

:root {
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}

.dish {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding: 12px;
  transition: 0.3s;
}
.dish:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}
.dish .image {
  height: 100px;
  width: 100px;
  background-color: #999999;
}
.dish .wrap {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.dish h4 {
  font-size: 1.25rem;
}
.dish .ingredients {
  display: flex;
  flex-direction: row;
  gap: 4px;
  margin-block-start: 8px;
  font-size: 0.875rem;
  color: #808080;
}
.dish li:not(:last-child):after {
  content: ", ";
}
.dish .price {
  display: flex;
  flex-direction: row;
  gap: 4px;
}
.dish .counter {
  align-self: center;
}

@media (min-width: 576px) {
  .dish {
    flex-direction: row;
    align-items: stretch;
  }
  .dish .wrap {
    justify-content: space-between;
  }
  .dish .counter {
    align-self: end;
  }
}